body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.car {
    position: absolute;
    height: 70px;
    width: 40px;
    z-index: 1;
}

.wrap-restart {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #292929;
    color: white;
    font-family: sans-serif;
    font-size: 40px;
    text-align: center;
    display: none;
    z-index: 10;
}

.wrap-restart.visible {
    display: block;
}

.restart {
    border: none;
    padding: 25px;
    color: white;
    background-color: #8a64ff;
    font-size: 30px;
    margin-top: 30%;
}

.pot {
    width: 25px;
    height: 25px;
    position: absolute;
}

.main {
    display: flex;
    justify-content: space-between;
}

.wrap-road {
    display: flex;
}

.action-game {
    margin-top: 50px;
    margin-left: 2px;
    border: none;
    color: white;
    width: 60px;
    height: 60px;
    font-family: sans-serif;
    background-color: #8a64ff;
    cursor: pointer;
    visibility: hidden;
}

.action-game.visible {
    visibility: visible;
}
.wrap-score {
    font-size: 35px;
    background-color: white;
    color: #454545;
    min-width: 250px;
    padding: 10px;
}

.score, .lap {
    margin-bottom: 5px;
    margin-top: 5px;
}

.lap {
    font-size: 30px;
    padding-bottom: 20px;
    border-bottom: 2px solid #8a64ff;
}

table {
    box-sizing: border-box;
    width: 100%;
    max-width: 100%;
    border-collapse: collapse;
    border: solid #e0e0dc;
    border-width: 1px 0 0 1px;
    font-size: 20px;
}

tr {
    margin: 0;
    padding: 0;
    border: 0;
}

td {
    border: solid #e0e0dc;
    border-width: 0 1px 1px 0;
    padding: 6px 8px;
    text-align: left;
}
.progress-root {
    height: 300px;
    margin-top: 20px;
    margin-left: 10px;
    width: 30px;
    background-color: #292929;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
}

.progress-root:after {
    content: "";
    width: 30px;
    height: 45px;
    position: absolute;
    top: 100%;
    z-index: -1;
    background-color: #292929;
}

.progress-hero {
    width: 20px;
    height:40px;
    position: absolute;
    bottom: 5px;
    left: 5px;
}

.progress-point {
    width: 20px;
    margin-left: 10px;
    height: 2px;
    background-color: white;
}

.progress-edge {
    width: 20px;
    height: 20px;
    position: absolute;
    right: -30px;
}

.progress-edge.start {
    bottom: 0;
}
.crashed {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
    -webkit-animation: go-invisible .25s infinite alternate;
            animation: go-invisible .25s infinite alternate;
}

@-webkit-keyframes go-invisible {
    from {
        opacity: .3;
    }
    to {
        opacity: 1;
    }
}

@keyframes go-invisible {
    from {
        opacity: .3;
    }
    to {
        opacity: 1;
    }
}
.road {
    position: relative;
    height: 85vh;
    width: 100%;
    max-width: 350px;
    background-color: #292929;
    overflow: hidden;
}

.line {
    position: absolute;
    height: 150px;
    width: 4%;
    margin-left: 48%;
    background-color: rgba(255,255,255,0.5);
}

.help {
    text-align: center;
}
