.car {
    position: absolute;
    height: 70px;
    width: 40px;
    z-index: 1;
}

.wrap-restart {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #292929;
    color: white;
    font-family: sans-serif;
    font-size: 40px;
    text-align: center;
    display: none;
    z-index: 10;
}

.wrap-restart.visible {
    display: block;
}

.restart {
    border: none;
    padding: 25px;
    color: white;
    background-color: #8a64ff;
    font-size: 30px;
    margin-top: 30%;
}

.pot {
    width: 25px;
    height: 25px;
    position: absolute;
}

.main {
    display: flex;
    justify-content: space-between;
}

.wrap-road {
    display: flex;
}

.action-game {
    margin-top: 50px;
    margin-left: 2px;
    border: none;
    color: white;
    width: 60px;
    height: 60px;
    font-family: sans-serif;
    background-color: #8a64ff;
    cursor: pointer;
    visibility: hidden;
}

.action-game.visible {
    visibility: visible;
}